import React, { useState } from "react";
import ImageFrame from "./ImageFrame";

function ImageSlider({ images }) {
  let [currentImageIndex, setCurrentImageIndex] = useState(0);
  const goToImage = (type) => {
    if (type === "next") {
      setCurrentImageIndex((prevIndex) => (prevIndex + 1) % images.length);
    } else {
      setCurrentImageIndex((prevIndex) => {
        if (prevIndex === 0) {
          return images.length + (prevIndex - 1);
        } else {
          return prevIndex - 1;
        }
      });
    }
  };

  return (
    <div>
      <div className="p-0 m-0 relative overflow-hidden">
        <ImageFrame image={currentImageIndex} images={images} />
        <div
          className="absolute p-4 md:p-8 top-1/4 md:top-1/3 right-full translate-x-3/4 translate-y-1/4 flex justify-center items-center rounded-2xl bg-sky-500/[.1] border-2 cursor-pointer"
          onClick={() => goToImage("prev")}
        >
          <i className="fa-solid fa-arrow-left text-3xl md:text-5xl text-sky-300"></i>
        </div>
        <div
          className="absolute p-4 md:p-8 top-1/4 md:top-1/3 right-0 translate-x-1/4 translate-y-1/4 flex justify-center items-center rounded-2xl bg-sky-500/[.1] border-2 cursor-pointer"
          onClick={() => goToImage("next")}
        >
          <i className="fa-solid fa-arrow-right text-3xl md:text-5xl text-sky-300"></i>
        </div>
      </div>
    </div>
  );
}

export default ImageSlider;
