import React, { useEffect } from "react";
import { useParams } from "react-router-dom";
import { Helmet } from "react-helmet-async";
import HtmlRender from "../components/HtmlRender";
import { Link } from "react-router-dom";
import { AuthticationService } from "../services/auth";
import ShimmerPost from "../components/ShimmerPost";
import { useNavigate } from "react-router-dom";
import ImageSlider from "../components/ImageSlider";
import CONSTANTS from "../assets/constants.json";

function Post({ posts }) {
  const navigate = useNavigate();
  const { id } = useParams();
  let post = posts.find((post) => post.id === parseInt(id));
  let firstId = 0;
  let lastId = 0;
  let sliderImages = CONSTANTS.HERO_IMAGES;
  if (posts && posts.length > 0) {
    [firstId, lastId] = [posts[posts.length - 1].id, posts[0].id];
    sliderImages = prepareSliderImages(post, [...CONSTANTS.HERO_IMAGES]);
  } else {
    navigate("/");
  }
  if (post) {
    const date = new Date(post.date);
    post.dateString =
      date.toLocaleString("default", { month: "long" }) +
      " " +
      date.getDate() +
      ", " +
      date.getFullYear();
  }

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  function prepareSliderImages(post, staticImages) {
    if (post.image1) {
      staticImages.unshift(post.image1);
    }
    if (post.image2) {
      staticImages.unshift(post.image2);
    }
    if (post.image3) {
      staticImages.unshift(post.image3);
    }
    if (post.image4) {
      staticImages.unshift(post.image4);
    }
    return staticImages.slice(0, 4);
  }
  return (
    <div className="w-full mx-auto md:p-0 md:m-0">
      <ImageSlider images={sliderImages} />

      {!post ? (
        <React.Fragment>
          <ShimmerPost />
        </React.Fragment>
      ) : (
        <React.Fragment>
          <Helmet>
            <title>{post.title}</title>
            <meta name="description" content={`Read ${post.title}.`} />
          </Helmet>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-y-6 md:gap-12 p-2 md:px-24 md:py-12">
            <div className="order-last md:order-first">
              <section className="border-2 border-blue-600/[0.2] shadow-md rounded-xl p-2 text-center bg-white">
                <h1 className="text-2xl">Article Information</h1>
                <hr></hr>
                <ul className="text-center space-y-2 p-4 ">
                  <li className="flex items-center gap-2">
                    <i className="fa fa-tag" aria-hidden="true"></i>
                    <span className="font-bold">Category:</span>
                    <span>Inspiration</span>
                  </li>
                  <li className="flex items-center gap-2">
                    <i className="fa-regular fa-clock"></i>
                    <span className="font-bold">Updated:</span>
                    <span>{post.dateString}</span>
                  </li>
                  <li className="flex items-center gap-2">
                    <i className="fa-regular fa-user"></i>
                    <span className="font-bold">Author:</span>
                    <span>{post.author}</span>
                  </li>
                </ul>
              </section>
            </div>

            <section className="col-span-3 border-2 border-blue-600/[0.2] shadow-md rounded-xl p-6 flex flex-wrap flex-row gap-4 md:gap-12 bg-white">
              <div className="md:basis-3/6 w-full">
                <img
                  src={post.cover}
                  alt="cover"
                  className="border-2 border-blue-600/[0.2] shadow-md rounded-xl w-full h-full max-h-56 md:max-h-96 "
                />
              </div>
              <div className="md:basis-2/5">
                <h3 className="text-3xl">{post.title}</h3>
                <li className="flex items-center gap-2">
                  <i className="fa-solid fa-calendar-days"></i>
                  <span className="font-bold text-md">Updated:</span>
                  <time>{post.dateString}</time>
                </li>
                {AuthticationService.isAuthenticated && (
                  <Link
                    to={`/edit-post/${post.id}`}
                    className="text-slate-700"
                    aria-label={`Click to Edit:${post.title}`}
                  >
                    <p className="mt-2">
                      <i className="fa fa-pencil text-md mr-1"></i>
                      <i>edit</i>
                    </p>
                  </Link>
                )}
              </div>
              <HtmlRender html={post.content} />

              <div
                className={`flex gap-6 md:gap-12 w-full ${
                  +id - 1 < firstId ? "justify-end" : "justify-between"
                }`}
              >
                {+id - 1 >= firstId && (
                  <Link
                    to={`/post/${+id - 1}`}
                    className="flex gap-1 md:gap-2 w-fit border-2 border-blue-600/[0.2] rounded-xl p-2 pl-0 md:p-4 md:pl-0 bg-white hover:bg-gray-800 hover:text-white text-md md:text-xl"
                    aria-label={`Read more about ${post.title}`}
                  >
                    <button className="bg-slate-400 px-1 md:px-4 pl-8 rounded-tr-3xl rounded-br-3xl">
                      <i className="fa-solid fa-arrow-left text-xl md:text-3xl"></i>
                    </button>
                    <h1 className="text-inherit">Previous article</h1>
                  </Link>
                )}
                {+id + 1 <= lastId && (
                  <Link
                    to={`/post/${+id + 1}`}
                    className="flex gap-1 md:gap-2 w-fit border-2 border-blue-600/[0.2] rounded-xl p-2 pr-0 md:p-4 md:pr-0 bg-white hover:bg-gray-800 hover:text-white text-md md:text-xl"
                    aria-label={`Read more about ${post.title}`}
                  >
                    <h1 className="text-inherit">Next article</h1>
                    <button className="bg-slate-400 px-1 md:px-4 pl-8 rounded-tl-3xl rounded-bl-3xl">
                      <i className="fa-solid fa-arrow-right text-xl md:text-3xl"></i>
                    </button>
                  </Link>
                )}
              </div>
            </section>
          </div>
        </React.Fragment>
      )}
    </div>
  );
}

export default Post;
